.btn-white-round-lg {
  cursor: pointer;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 1000px;
  padding: 1.65rem 5.5rem;
  font-size: 1.75rem;
  font-weight: 400;
  line-height: 150%;
  background-color: $white;
  color: $black;
  border: 1px solid $white;
  outline: none;
}
.btn-white-round-sm {
  cursor: pointer;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 1000px;
  padding: 1.25rem 3.5rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 150%;
  background: transparent;
  color: $white;
  border: 1px solid $white;
  outline: none;
}
.btn-white-round-lg-outlined {
  cursor: pointer;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 1000px;
  padding: 1.65rem 5.5rem;
  font-size: 1.75rem;
  font-weight: 400;
  line-height: 150%;
  background: transparent;
  color: $white;
  border: 1px solid $white;
  outline: none;
  @media only screen and (max-width: 650px) {
    width: 100%;
  }
}
.btn-white-round-sm-outlined {
  cursor: pointer;
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 1000px;
  padding: 1.25rem 3.5rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 150%;
  background: transparent;
  color: $white;
  border: 1px solid $white;
  outline: none;
  transition: color 0.3s ease, background-color 0.3s ease;

  // &:hover {
  //   color: #000000;
  //   background-color: #ffffff;
  // }

  @media only screen and (max-width: 650px) {
    width: 100%;
  }
    
  }


